<template>
  <div class="fromBox" :UserListData="UserListData">
    <el-form :label-position="labelPosition" label-width="100px" :model="fromData" :rules="rules" ref="fromData">
      <el-form-item label="用户商家名" prop="companyName">
        <el-select v-model="fromData.companyId" placeholder="请选择" style="width: 100%">
          <el-option v-for="item in options" :key="item.code" :label="item.CN" :value="item.code"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="登陆账号" prop="account">
        <el-input v-model="fromData.account"></el-input>
      </el-form-item>
      <el-form-item label="登陆密码" prop="password">
        <el-input v-model="fromData.password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="">
        <div style="text-align: left" :CompanyListData="CompanyListData">
          <el-button type="primary" round class="submit" v-on:click="submitForm('fromData')"> 确定 </el-button>
        </div>
      </el-form-item>
    </el-form>
    <!-- 表单-->
  </div>
</template>
<style scoped>
  .submit{ width: 130px;height: 34px;line-height: 0px;background-color: #0e72b9;margin-top:40px;}
  .submit:hover{background-color: #409eff }
.fromBox{width: 500px;border: 1px solid #e1e1e1;margin: auto;background-color: white;padding: 40px 20px 40px 20px; border-radius:8px; box-shadow:0 0 16px #e2e7ea;padding-bottom: 10px; }
</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  import {mapState, mapActions} from 'vuex'
  export default {
    name: 'my-addUserFrom',
    data () {
      // 自定义密码校验
      var indeofPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        labelPosition: 'left',
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        fromData: {
          companyId: null, // 商家名
          account: null, // 账号
          password: null // 密码
        },
        rules: {
          companyId: [
            { required: true, message: '请选择用户商家', trigger: 'change' },
          ],
          account: [
            { required: true, message: '请输入登陆账号', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '请输入登陆密码', trigger: 'blur' },
          ]
        },
     options: [{
        code: '1',
        CN: '大洋运输',
        EN: ''
      }],
        left: 'left'
      }
    },
    methods: {
      ...mapActions({
        addUser: 'addUser',
        getUserList: 'getUserList',
        getCompanyList: 'getCompanyList'
      }),
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      login () {
        this.$router.push({ name: "map" })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var parm = {
              companyId: this.fromData.companyId, // 商家名
              account: this.fromData.account, // 账号
              password: this.fromData.password // 密码
            }
            this.addUser(parm)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      companyListFn (res) {
        res = res.data
        this.options = []
        for (let i in res) {
          var pu =  {
              code: res[i].id,
              CN: res[i].name,
              EN: ''
            }
          this.options.push(pu)
        }
      },
      listStatus (res) {
        this.loading = false
        // 清空 vuex 中的本次记录，以免被下次操作状态造成混淆
        this.$store.commit('updateStatusTo', {body: null})
        this.fromData = {
          companyId: null, // 商家名
          account: null, // 账号
          password: null // 密码
        }
        if (res.status == 0) {
          // 修改成功
          this.openSuccess(res.message)
        }
        if (res.status != 0) {
          // 修改成功 修改失败
          var text = ''
          if(res.status == 6){
            text = '该账号已存在'

          }else {
           text = res.message
          }
          this.openError(text)
        }
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      }
    },
    computed: {
      UserListData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
      updateStatusData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
      CompanyListData: function () {
        var parm = this.CompanyList
        if (parm) {
          this.companyListFn(parm.body)
        }
        return 1
      },
      ...mapState([
        'updateStatus',
        'language',
        'UserList',
        'CompanyList'
      ])
    },
    mounted () {
    var parm ={
      userId:''
    }
    this.getCompanyList(parm)
    },
    components: {
    }
  }
</script>




